import {createEnhancedElement} from '@servicenow/library-enhanced-element';
import styles from './_now-image.scss';
import {toNumber, isString, isNumber} from 'lodash';

/*
 * TODO: This is a temporary way of debugging customer problems during the
 * Seismic 18 upgrade process, because tectonic resolution makes it hard to
 * know what version of a component is running. Remove when not needed.
 * Intentionally runs in dev + production environments so we can debug all builds.
 */
const {name, version} = require('../package.json');
const registry = (window.___NOW_DESIGN_SYSTEM_PACKAGES___ =
	window.___NOW_DESIGN_SYSTEM_PACKAGES___ || {});
if (!registry[name]) {
	registry[name] = version;
}

const toValidPixelsOrUndefined = (v) => {
	// isString or isNumber protects again toNumber(false) or toNumber(null)
	// v !== '' protects against toNumber('')
	// isFinite(toNumber(v)) protects against `Infinity` etc.
	if ((isString(v) || isNumber(v)) && v !== '' && isFinite(toNumber(v))) {
		return toNumber(v) + 'px';
	}
	return undefined;
};

const view = (state) => {
	const {src, sources, alt, width, height, fit, position} = state.properties;
	return (
		<picture>
			{sources.map((source) => {
				return (
					<source
						type={source.type}
						media={source.media}
						srcset={source.srcset}
						sizes={source.sizes}
					/>
				);
			})}
			<img
				src={src}
				alt={alt}
				style={{
					objectFit: fit,
					objectPosition: position,
					width: toValidPixelsOrUndefined(width),
					height: toValidPixelsOrUndefined(height)
				}}
			/>
		</picture>
	);
};

/**
 * A simple, responsive image component.
 * Recommended for use in UI Builder. If using a different interface,
 * use a native `img` or `picture` tag instead.
 *
 * ```jsx
 * <now-image src="./image.png" alt="description" />
 *
 * <now-image src="./image.png" alt="description" height="50" width="50" fit="cover" position="left" />
 *
 * const sources = [
 *   {srcset: './image.webp', type: 'image/webp', media: '(min-width: 800px)'}
 *   {srcset: './image.png', type: 'image/png', media: '(max-width: 800px)'}
 * ];
 * <now-image src="./image.jpg" sources={sources}  alt="description" />
 * ```
 *
 * @seismicElement now-image
 * @summary A simple, responsive image component.
 * @uib.label Image
 * @uib.icon image
 * @uib.description Displays a static image
 * @uib.category primitives
 * @uib.associatedTypes global.core, global.dashboard, global.landing-page
 * @uib.properties alt, src, fit, position, height
 */
createEnhancedElement('now-image', {
	properties: {
		/**
		 * Required. The image URL.
		 * @type {string}
		 *
		 * @uib.required
		 * @uib.label Image source
		 * @uib.description URL of the image to show
		 * @uib.defaultValue /now-image-placeholder.jpg
		 */
		src: {required: true, schema: {type: 'string'}},
		/**
		 * An array of source objects to use in responsive images,
		 * where each source object can contain:
		 * - `srcset`: Image URL (required) or comma-separated list of image
		 * URLs with optional width and/or pixel density descriptors.
		 * - `type`: MIME type of the image source.
		 * - `media`: Valid media query for the image source.
		 * - `sizes`: Comma-separated list of source sizes that describes the
		 * rendered width of the image represented by the source.
		 * @type {{srcset: string, type: string, media: string, sizes: string}[]}
		 */
		sources: {
			default: [],
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						srcset: {type: 'string'},
						type: {type: 'string'},
						media: {type: 'string'},
						sizes: {type: 'string'}
					},
					required: ['srcset']
				}
			}
		},
		/**
		 * Alternative text description of the image.
		 * @type {string}
		 *
		 * @uib.required
		 * @uib.label Alt text
		 * @uib.description Description of the image
		 * @uib.defaultValue
		 * @uib.translatable true
		 */
		alt: {required: true, schema: {type: 'string'}},
		/**
		 * Intrinsic width of the image in pixels.
		 * @type {number}
		 */
		width: {schema: {type: 'number'}},
		/**
		 * Intrinsic height of the image in pixels.
		 * @type {number}
		 *
		 * @uib.description Static height for the image (uses image height by default)
		 */
		height: {schema: {type: 'number'}},
		/**
		 * Value passed to the `object-fit` CSS property on the image. This
		 * value dictates how the image fits into its container when the
		 * `height` and `width` properties differ from the image dimensions.
		 * @type {('fill'|'contain'|'cover'|'none'|'scale-down')}
		 *
		 * @uib.required
		 * @uib.description How the image fits in its container
		 * @uib.fieldType choice
		 * @uib.typeMetadata.choices [{"label": "Expand to fill", "value": "cover"}, {"label": "Stretch to fit", "value": "fill"}, {"label": "Keep original size", "value": "none"}]
		 * @uib.defaultValue cover
		 */
		fit: {
			schema: {
				type: 'string',
				enum: ['fill', 'contain', 'cover', 'none', 'scale-down']
			}
		},
		/**
		 * Value passed to the `object-position` CSS property on the image.
		 * This value dictates how the image is positioned relative to its
		 * container when the `height` and `width` properties differ from the
		 * image dimensions.
		 * @type {string}
		 *
		 * @uib.required
		 * @uib.description Anchors the image if it is larger than its container
		 * @uib.fieldType choice
		 * @uib.typeMetadata.choices [{"label": "Center", "value": "center center"}, {"label": "Top-Left", "value": "top left"}, {"label": "Top-Right", "value": "top right"}, {"label": "Bottom-Left", "value": "bottom left"}, {"label": "Bottom-Right", "value": "bottom right"}]
		 * @uib.defaultValue center center
		 */
		position: {schema: {type: 'string'}}
	},
	view,
	styles
});
